import React from "react";
import "./Modal.css";

class Modal extends React.Component {
  close = () => {
    const modal = document.querySelector(".modal");
    modal.style.display = "none";
  };

  render() {
    return (
      <div className="modal">
        <div className="modal-inner">
          <div className="title">
            <h1>RULES</h1>
            <button onClick={this.close}>X</button>
          </div>
          <div className="rules">
            <img
              src="https://upload.wikimedia.org/wikipedia/commons/b/bb/Rock-paper-scissors-ru.svg"
              alt="rules"
            />
          </div>
        </div>
      </div>
    );
  }
}

export { Modal };
