import "./Body.css";
import React from "react";
import useSound from "use-sound";
import quack from "./quack.mp3";

const Rock = (props) => {
  const [playActive] = useSound(quack, { volume: 0.25 });
  return (
    <i
      data-number="1"
      onClick={props.handleHeroChoice}
      onMouseDown={playActive}
      className="fas fa-hand-rock rock"
    />
  );
};

const Paper = (props) => {
  const [playActive] = useSound(quack, { volume: 0.25 });
  return (
    <i
      data-number="1"
      onClick={props.handleHeroChoice}
      onMouseDown={playActive}
      className="fas fa-hand-paper paper"
    />
  );
};

const Scissors = (props) => {
  const [playActive] = useSound(quack, { volume: 0.25 });
  return (
    <i
      data-number="1"
      onClick={props.handleHeroChoice}
      onMouseDown={playActive}
      className="fas fa-hand-scissors scissors"
    />
  );
};

class Body extends React.Component {
  state = {
    choice: <i className="fas fa-question" />,
    score: 0,
    hero: 0,
  };

  handleHeroChoice = (e) => {
    this.setState({ hero: e.target.dataset.number });

    let items = [
      <i className="fas fa-hand-rock rock" />,
      <i className="fas fa-hand-paper paper" />,
      <i className="fas fa-hand-scissors scissors" />,
    ];

    let i = Math.floor(Math.random() * 3);
    this.setState({ choice: items[i] });

    setTimeout(() => {
      if (
        this.state.hero === "1" &&
        this.state.choice.props.className === "fas fa-hand-scissors scissors"
      ) {
        this.setState(({ score }) => ({
          score: score + 1,
        }));
      }
      if (
        this.state.hero === "1" &&
        this.state.choice.props.className === "fas fa-hand-paper paper"
      ) {
        this.setState(({ score }) => ({
          score: score - 1,
        }));
      }
      if (
        this.state.hero === "1" &&
        this.state.choice.props.className === "fas fa-hand-rock rock"
      ) {
        this.setState(({ score }) => ({
          score: score + 0,
        }));
      }
      if (
        this.state.hero === "2" &&
        this.state.choice.props.className === "fas fa-hand-rock rock"
      ) {
        this.setState(({ score }) => ({
          score: score + 1,
        }));
      }
      if (
        this.state.hero === "2" &&
        this.state.choice.props.className === "fas fa-hand-scissors scissors"
      ) {
        this.setState(({ score }) => ({
          score: score - 1,
        }));
      }
      if (
        this.state.hero === "2" &&
        this.state.choice.props.className === "fas fa-hand-paper paper"
      ) {
        this.setState(({ score }) => ({
          score: score + 0,
        }));
      }
      if (
        this.state.hero === "3" &&
        this.state.choice.props.className === "fas fa-hand-paper paper"
      ) {
        this.setState(({ score }) => ({
          score: score + 1,
        }));
      }
      if (
        this.state.hero === "3" &&
        this.state.choice.props.className === "fas fa-hand-rock rock"
      ) {
        this.setState(({ score }) => ({
          score: score - 1,
        }));
      }
      if (
        this.state.hero === "3" &&
        this.state.choice.props.className === "fas fa-hand-scissors scissors"
      ) {
        this.setState(({ score }) => ({
          score: score + 0,
        }));
      }
    }, 0);
  };

  render() {
    return (
      <div className="body">
        <div className="body-main">
          <div className="body-left">
            <Rock handleHeroChoice={this.handleHeroChoice} />
            <Paper handleHeroChoice={this.handleHeroChoice} />
            <Scissors handleHeroChoice={this.handleHeroChoice} />
          </div>
          <div className="body-right">
            <p>{this.state.choice}</p>
          </div>
        </div>

        <div className="body-bottom">Score: {this.state.score}</div>
      </div>
    );
  }
}

export { Body };
